a {
  text-decoration: none;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.toy {
  margin: 20px;
}

.details-container {
  display: flex;
  flex-wrap: wrap;
  max-width: 400px;
  margin: auto;
}

.details-container div{
  background-color: rgb(234 234 234);
  flex-basis: 100%;
  margin-bottom: 20px;
}

.toy-preview div{
  background-color: rgb(234 234 234);
}

.details-container a:first-of-type{
  flex-grow: 1;
  /* margin-inline-end: 100px; */
}


.toy-preview img {
  height: 20px;
}

.toy-details {
  margin-bottom: 20px;
}

/* HELPERS */

.clean-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.flex {
  display: flex;
}

.wrap {
  flex-wrap: wrap;
}

.pointer {
  cursor: pointer;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}